@import url("https://fonts.googleapis.com/css?family=BenchNine");
@import url("https://fonts.googleapis.com/css?family=Khand");

.gameboy-button {
  width: 100%;
  min-width: 5vmin;
  height: 100%;
  min-height: 5vmin;
  position: relative;

  svg {
    width: 100%;
    height: 100%;

    text {
      font-size: 90px;
      font-weight: bold;
      font-family: "BenchNine", sans-serif;
      text-transform: capitalize;
    }
  }

  &__external-text--gba {
    pointer-events: none;

    position: absolute;
    top: -2vw;
    left: calc(-10vw - 10px);

    width: 8vw;
    transform: rotate(15deg);

    text-transform: capitalize;
    font-family: "BenchNine", sans-serif;

    color: #5f68ab;
    font-size: 4vw;
    text-shadow: 0px 0px 2px #fdfbff;

    border: 2px solid rgba(0, 0, 0, 0.35);
    border-radius: 25px;

    padding-right: calc(10.15vw - 100%);
    padding-left: 10px;
  }

  &__external-text--gbc {
    pointer-events: none;

    position: absolute;
    bottom: -9vw;
    left: 1vw;

    text-transform: capitalize;
    font-family: "Khand", sans-serif;

    color: #8128be;
    font-size: 7vw;
    text-shadow: 0px 0px 1px #fdfbff;
  }

  &__external-text--expanded {
    display: none;
  }
}



